import { observable } from 'mobx'
import guid from "./guid"

class Bullet {
    @observable currentCoordinate = { x: null, y: null }
    @observable angle = 0;
    constructor() {
        this.id = guid()
        this.willHit = false
        this.sourceUnit = {}
        this.targetUnit = {}
        this.sourceCoordinate = {}
        this.velocity = {}
    }
}

export default Bullet



