import React, { Component } from 'react';
import { observer } from 'mobx-react';

@observer
export default class Line extends Component {

  // static propTypes = {
  //   from: React.PropTypes.shape({
  //     x: React.PropTypes.number.isRequired,
  //     y: React.PropTypes.number.isRequired,
  //   }),
  //   to: React.PropTypes.shape({
  //     x: React.PropTypes.number.isRequired,
  //     y: React.PropTypes.number.isRequired,
  //   }),
  //   style: React.PropTypes.string
  // };

  render() {
    const { unit, offset, color } = this.props;

    // if (unit.currentAction.target_unit.dead) return null;
    if (!unit.targetCoordinates) return null;

    let from = { x: unit.x, y: unit.y }
    let to={ x: unit.targetCoordinates.x, y: unit.targetCoordinates.y }

    if (to.x < from.x) {
        let tmp = from;
        from = to;
        to = tmp;
    }

    const len = Math.sqrt(Math.pow(from.x - to.x, 2) + Math.pow(from.y - to.y, 2));
    const angle = Math.atan((to.y - from.y) / (to.x - from.x));

    const finalX = from.x - .5 * len * (1 - Math.cos(angle));
    const finalY = (from.y + .5 * len * Math.sin(angle)) + offset;

    const style = {
      transform: `translate(${finalX}px, ${finalY}px) rotate(${angle}rad)`,
      width: `${len}px`,
      height: `${0}px`,
      borderBottomColor: color,
    };

    return <div className="line" style={style}></div>;
  }
}



