import { observable, action } from 'mobx'
import _ from 'lodash'
import Victor from 'victor' // Vector processing

import { createUnit } from "./Unit" 
import Bullet from "./Bullet"
import Board from "./Board"
import { actionTypes, directions, gameParams } from "./gameInfo"

class Game {
    @observable tickCount = 0;
    @observable board;

    constructor(config) {
        this.defaultTickRate = 50
        this.config = config
        this.initialize()
    }

    initialize() {
        this.tickCount = -1
        this.board = new Board()

        // Place units relative to the center of the board
        var maxX = _.max(_.map(this.config.units, u => u.x));
        var maxY = _.max(_.map(this.config.units, u => u.y));
        var originX = (this.board.width / 2.0) - (maxX / 2.0);
        var originY = (this.board.height / 2.0) - (maxY / 2.0);
        for(var u of this.config.units) {
            var unit = createUnit({...u, ...{ x: u.x + originX, y: u.y + originY }})
            this.board.addUnit(unit)
        }
    }

    play() { this.changeSpeed() }
    fastMotion() { this.changeSpeed(10) }
    slowMotion() { this.changeSpeed(100) }

    changeSpeed(millisecondsPerTick) {
        this.pause()
        this.start(millisecondsPerTick)
    }

    pause() {
        clearInterval(this.ticker);
        this.ticker = null;
    }

    start(tickRate) {
        if (!this.ticker)
          this.ticker = setInterval(() => this.tick(), tickRate || this.defaultTickRate);
    }

    @action tick() {
        this.tickCount += 1;

        // Move bullets
        this.board.bullets.forEach(bullet => this.moveBullet(bullet))

        // Generate modifier functions
        let modifierFunctions = []
        for(var unit of this.board.units) {
            this.fetchNextAction(unit);
            modifierFunctions.push(unit.performAction());
        }

        // Execute the modifier functions (ie make changes to board)
        // These modifier functions do not read the current state of board data and
        // only make changes.  This ensures that changes made do not affect later changes made
        // in the same tick
        modifierFunctions.forEach(f => f())

        this.regenerateShield()
    }

    regenerateShield() {
        this.board.units
             .filter(u => !u.dead)
             .filter(u => u.shield < u.unitType.shield && u.lastDamageTick < (this.tickCount - 100))
             .forEach(u => { u.shield += _.min([50, u.unitType.shield - u.shield]) })
    }

    fetchNextAction(unit) {
        if (this.tickCount % 50 === 0) {
            // Find next action from the unit's AI
            const newAction = unit.ai.findNextAction(unit, this.board)

            switch(newAction.actionType) {
                case actionTypes.move:
                    unit.currentAction = newAction
                    unit.performAction = this.moveUnit(unit)
                    break;
                case actionTypes.attack:
                    // If the unit is already attacking the unit then do not initiate a new attack
                    if (unit.currentAction.actionType === actionTypes.attack &&
                        newAction.target_unit === unit.currentAction.target_unit) {
                        return
                    }
                    unit.currentAction = newAction
                    unit.performAction = this.initiateNewAttack(unit)
                    break;
            }
        }
    }

    moveUnit(unit) {
        return () => {
            // Kill units when they thit the wall
            if (unit.x < 20 || unit.y < 20 || unit.x > this.board.width - 20 || unit.y > this.board.height - 20) {
                return () => {
                  this.killUnit(unit)
                }
            }

            var { x:sourceX, y:sourceY, currentAction: { direction, target_unit, target_unit: { x: targetX, y: targetY }}} = unit; 

            // Do nothing if the target unit is dead
            if(!this.unitIsAlive(target_unit)) { return () => {} }

            var diff = new Victor(targetX - sourceX, targetY - sourceY).normalize();
            var angle = Math.abs(diff.angleDeg()) - 90;
            var diffX = _.round(diff.x * unit.unitType.travelSpeed)
            var diffY = _.round(diff.y * unit.unitType.travelSpeed)
            return () => {
              if(direction === directions.towards) {
                  unit.x += diffX
                  unit.y += diffY
              } else if (direction === directions.away) {
                  unit.x -= diffX
                  unit.y -= diffY
              }
              unit.rotation = _.round(angle);
              unit.shootingLevel = 0
            }
        }
    }

    initiateNewAttack(unit) {
        var { currentAction: { target_unit } } = unit
        var bulletTick = -1
        var targettingTime = 45
        return () => {
            // Do nothing if the target unit is dead
            if(!this.unitIsAlive(target_unit)) {
                return () => {
                    unit.shootingLevel = 0
                }
            }

            bulletTick += 1;
            bulletTick = bulletTick % 54;
            return () => {
                if (bulletTick < targettingTime) {
                    unit.shootingLevel =  _.round(bulletTick / targettingTime * 100)
                }
                if (bulletTick > targettingTime && bulletTick % 1 === 0) {
                    unit.shootingLevel =  _.round(100 - (bulletTick - targettingTime) / (54 - targettingTime) )
                    this.createBullet(unit, target_unit)
                }
            }
        }
    }

    createBullet(sourceUnit, targetUnit) {
        var diff = new Victor(targetUnit.x - sourceUnit.x, targetUnit.y - sourceUnit.y).normalize();

        var bullet = new Bullet()
        bullet.sourceUnit = sourceUnit
        bullet.targetUnit = targetUnit
        bullet.sourceCoordinate = { x: sourceUnit.x, y: sourceUnit.y }
        bullet.currentCoordinate = { x: sourceUnit.x, y: sourceUnit.y }
        bullet.willHit = _.sample([true, false])
        bullet.angle = Math.abs(diff.angleDeg());
        bullet.velocity = { x: diff.x * gameParams.bulletVelocity, y: diff.y * gameParams.bulletVelocity }

        this.board.bullets.push(bullet)
    }

    unitIsAlive(unit) {
        return _.includes(this.board.units, unit)
    }

    killUnit(unit) {
        unit.dead = true; // used when other objects reference this unit
        this.board.units.remove(unit)
    }

    moveBullet(bullet) {
        const { currentCoordinate: { x, y } } = bullet;

        // Bullet has left the board
        if (x < 0 || x > this.board.width || y < 0 ||  y > this.board.height ) {
            this.board.bullets.remove(bullet);
            return
        }

        bullet.currentCoordinate.x += bullet.velocity.x
        bullet.currentCoordinate.y += bullet.velocity.y

        if(bullet.willHit) {
            if(!this.unitIsAlive(bullet.targetUnit)) {
                // The target unit has been killed
                bullet.willHit = false;
                return;
            }
            var sourcePosition = Victor.fromObject(bullet.sourceCoordinate)
            var currentPosition = Victor.fromObject(bullet.currentCoordinate)
            var unitPosition = Victor.fromObject(bullet.targetUnit)
            var distanceTravelled = sourcePosition.distance(currentPosition)
            var distanceToUnit = sourcePosition.distance(unitPosition)

            if (distanceTravelled >= distanceToUnit) {
                // bullet has just reached the unit
                if(distanceTravelled > gameParams.largeCircleRadius) {
                    bullet.willHit = false;
                } else {
                    var chance;
                    var attackRange = bullet.sourceUnit.unitType.attackRange
                    if (distanceTravelled <= gameParams.smallCircleRadius) {
                        chance = attackRange.short
                    } else if (distanceTravelled <= gameParams.mediumCircleRadius) {
                        chance = attackRange.medium
                    } else if (distanceTravelled <= gameParams.largeCircleRadius) {
                        chance = attackRange.long
                    }

                    if (Math.random(100) <= chance) {
                      // Bullet has hit the target
                      bullet.targetUnit.damage(bullet.sourceUnit.unitType.damagePerBullet)
                      bullet.targetUnit.lastDamageTick = this.tickCount
                      this.board.bullets.remove(bullet);
                      if(bullet.targetUnit.hp <= 0) {
                          this.killUnit(bullet.targetUnit)
                      }
                    } else {
                      // Bullet has missed
                      bullet.willHit = false;
                    }
                }
            }
        }
    }
}

export default Game



