import React, { Component } from 'react';
import { observer } from 'mobx-react';

@observer
class BulletView extends Component {
    render() {
        const { x, y } = this.props.bullet.currentCoordinate
        return (
                <div className="bullet" style={{transform: "translate("+x+"px, "+y+"px)"}}></div>
        )
    }
}

export default BulletView



