import { observable, computed } from 'mobx'
import guid from "./guid"

import { unitTypes, actionTypes } from "./gameInfo"

export const createUnit = ({ ai, x, y, team, type, type: { hp, shield }}) => {
    var unit = new Unit()
    unit.ai = ai
    unit.rotation = 0
    unit.x = x
    unit.y = y
    unit.team = team
    unit.unitType = type
    unit.hp = hp
    unit.shield = shield
    return unit;
}

class Unit {
    @observable unitType = unitTypes.assault;
    @observable x=0;
    @observable y=0;
    @observable rotation=0;
    @observable.ref currentAction = { target_unit: null, actionType: actionTypes.idle }
    @observable hp;
    @observable shield;
    @observable shootingLevel=0
    @observable showDistanceRings=false;
    @observable dead=false;

    constructor() {
        this.id = guid()
        this.ai = null
        this.performAction = () => { return () => {} }
    }

    @computed get shieldPercentage() {
        return this.shield / this.unitType.shield * 100.0
    }

    @computed get hpPercentage() {
        return this.hp / this.unitType.hp * 100.0
    }

    @computed get targetCoordinates() {
        if(this.currentAction.target_unit && !this.currentAction.target_unit.dead) {
            return { x: this.currentAction.target_unit.x, y: this.currentAction.target_unit.y }
        }
    }

    toggleDistanceRings() {
        this.showDistanceRings = !this.showDistanceRings;
    }

    damage(amount) {
        if (this.shield >= amount) {
            this.shield -= amount
        } else if (this.shield > 0) {
            amount -= this.shield
            this.shield = 0 
            this.hp -= amount
        } else {
            this.hp -= amount
        }
    }
}

export default Unit



